<template>
    <div>
        hello
    </div>
</template>
<script>
    import insertText from '../util/insertText';
    import Upload from './upload.vue';

    export default {
        name: 'iEditor',
        components: { Upload },
        props: {
            affix: Boolean,
            offsetTop: Number,
            placeholder: String,
            autosize: Object,
            writeName: {
                type: String,
                default: '内容'
            },
            changeScroll: Boolean,
            cover: String,
            showSummary: {
                type: Boolean,
                default: false
            }
        },
        data () {
            return {
                tabType: 'write',  // write || preview || summary
                content: '',
                showMdTip: false,
                showDiff: false,
                showDiffEditor: false,
                summary: ''
            };
        },
        watch: {
            showDiff (val) {
                // 避免出现输入框的滚动条
                this.$nextTick(() => {
                    this.showDiffEditor = val;
                });
            }
        },
    }
</script>


